

































import { v4 as uuidv4 } from "uuid";
import { NIL as uuidNIL } from "uuid";

import * as Typ from "../../types/index";
import {
  mdiAccount,
  mdiPencil,
  mdiShareVariant,
  mdiDelete,
  mdiAppleKeyboardControl,
} from "@mdi/js";

import Vue from "vue";

import ToolCard from "./ToolCard.vue";

export default Vue.extend({
  name: "ToolListItem",

  components: {
    ToolCard,
  },

  //PROPS
  props: {
    propToolKV: {
      type: Object as () => Typ.toolKeyValue,
    },
    btnText: {
      type: String,
      default: "Add",
    },

    propToolRating: {
      type: Object as () => Typ.toolRating,
      default: undefined,
    },
  },

  //DATA
  data() {
    return {
      moduleState: Typ.simpleModuleState.minimized as Typ.simpleModuleState,

      icons: {
        mdiAccount,
        mdiPencil,
        mdiShareVariant,
        mdiDelete,
        mdiAppleKeyboardControl,
      },

      debug: true as boolean,
    };
  },

  //METHODS
  methods: {
    btnEdit() {
      const appendix: string = this.propToolKV.key;
      this.navigateTo("/ToolCreation/Update/" + appendix);
    },
    btnDelete() {
      this.$store.commit("removeTool", this.propToolKV);
    },

    navigateTo(route: string): void {
      this.$router.push(route);
    },
  },
});
